@import url("https://fonts.googleapis.com/css2?family=Dongle&family=Jua&display=swap");
* {
  font-family: "Dongle", sans-serif;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 700px) {
  .App {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: white;
  }
  .navtop {
    position: absolute;
    top: 0;
    width: 100%;
    height: 8%;
    background-color: #1B1A17;
    color: white;
    padding: 2.5%;
    display: flex;
    align-items: center;
    font-size: 2rem;
  }
  .navtop .navtopright {
    width: 40%;
    display: flex;
    justify-content: left;
  }
  .navtop .navtopleft {
    width: 80%;
    display: flex;
    justify-content: right;
  }
  .content {
    position: absolute;
    top: 8%;
  }
  .main {
    color: #1B1A17;
    padding: 5%;
    width: 100%;
    height: 92%;
    position: relative;
    background: linear-gradient(white, #dadada);
  }
  .main .lastbox {
    border: #1B1A17;
    position: absolute;
    bottom: 0%;
    left: 0%;
    right: 0%;
    width: 100%;
    height: 10%;
    background-color: #1B1A17;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main .lastbox .ldiv {
    width: 33.3%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main .btn {
    position: absolute;
    bottom: 10px;
    width: 80px;
    height: 80px;
    background-color: white;
    border-radius: 40px;
    border: 3px solid #1B1A17;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main .btn P {
    font-size: 1rem;
  }
  .daybox {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 6vh;
    height: 10%;
  }
  .daybox h3 {
    color: #1B1A17;
  }
  .box {
    transition: all 1s;
    margin-left: 5%;
    margin-right: 5%;
    height: 10%;
    width: 90%;
    background-color: white;
    border: 3px solid #1B1A17;
    border-radius: 5vh;
    margin-top: 5%;
    margin-bottom: 15%;
    position: relative;
  }
  .box .boxleft {
    position: absolute;
    left: 0;
    width: 80%;
    height: 100%;
    padding: 2.5%;
    display: flex;
    padding-top: 1%;
  }
  .box .boxleft .boxleftleft {
    margin-left: 10%;
    margin-right: 10%;
    font-size: 4vh;
  }
  .box .boxright {
    z-index: 10;
    position: absolute;
    right: -5%;
    top: -2vh;
    width: 10vh;
    height: 10vh;
    border-radius: 50%;
    border: 5px solid #1B1A17;
    background-color: white;
  }
  .box .boxright .boxrightcontent {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .box .boxright .boxrightcontent p {
    color: #1B1A17;
    font-size: 5vh;
  }
  .doclick {
    -webkit-animation: spin 1s 1;
            animation: spin 1s 1;
  }
}
@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg) scale(0.9);
  }
  50% {
    transform: rotate(180deg) scale(1.25);
  }
  100% {
    transform: rotate(0deg) scale(1);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg) scale(0.9);
  }
  50% {
    transform: rotate(180deg) scale(1.25);
  }
  100% {
    transform: rotate(0deg) scale(1);
  }
}
.contnetbox {
  overflow: scroll;
  height: 80%;
  overflow-x: hidden;
}

.contnetbox::-webkit-scrollbar {
  width: 10px;
}

.contnetbox::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

.contnetbox::-webkit-scrollbar-track {
  background-color: white;
  border-radius: 10px;
}

.card {
  position: relative;
  width: 100%;
  height: 25%;
  border-radius: 25px;
  background-color: white;
  margin-bottom: 15%;
  margin-top: 5%;
  box-shadow: 3px 3px 5px 3px gray;
  font-size: 4.5vh;
}
.card .title {
  z-index: 10;
  position: absolute;
  top: -21%;
  width: 100%;
  text-align: center;
}
.card .cardcontent {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 5%;
}
.card .cardcontent div {
  display: flex;
}
.card .cardcontent .weektext {
  height: 33.3%;
  width: 50%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}
.card .week {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 10%;
}

.homebtn {
  position: absolute;
  left: 0px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  left: calc(50% - 25px);
  background-color: black;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.setcard {
  overflow: scroll;
  width: 100%;
  height: auto;
  border-radius: 20px;
  border: 1px solid black;
  box-shadow: 3px 3px 5px 3px gray;
  font-size: 3rem;
  padding: 2.5%;
}/*# sourceMappingURL=App.css.map */